import { createApp } from 'vue'
import App from './App.vue'

createApp(App).mount('#app')

import "@/assets/fonts.css"
import "@/assets/style.scss"

let roomCards = document.querySelector(".room__cards")

function anim(entries) {
    // console.log(entries);
    if (entries[0].isIntersecting) {
        roomCards.classList.add("active")
    } else {
        roomCards.classList.remove("active")
    }
}

let room = new IntersectionObserver(anim, {threshold: 0.5})
room.observe(roomCards)

let shopCards = document.querySelector(".shop__cards")

function anima(entries) {
    // console.log(entries);
    if (entries[0].isIntersecting) {
        shopCards.classList.add("active")
    } else {
        shopCards.classList.remove("active")
    }
}
let shop = new IntersectionObserver(anima, {threshold: 0.5})
shop.observe(shopCards)


let reviCards = document.querySelector(".revi__cards")
let reviImg = document.querySelector(".revi__stars")

function anime(entries) {
    // console.log(entries);
    if (entries[0].isIntersecting) {
        reviImg.classList.add("active")
    } else {
        reviImg.classList.remove("active")
    }
}

let revi = new IntersectionObserver(anime, {threshold: 0.5})
revi.observe(reviCards)

let navMenu = document.querySelector(".nav__menu") 
let navList = document.querySelector(".nav__list")

navMenu.addEventListener("click", () => {
    navList.classList.toggle("active")
})

